:root {
  --pf-v5-global--FontSize--md: 14px;
  --pf-global--FontSize--md: 14px;
  --pf-v5-global--FontSize--sm: 13px;
  --pf-global--FontSize--sm: 13px;
}

.odc-add-page-layout .ocs-page-layout__title {
  margin-top: 0;
  padding-top: var(--pf-v5-global--spacer--lg);
}

.odc-add-page-layout .ocs-page-layout__hint {
  padding-bottom: 0;
  padding-right: 0;
}

.odc-add-page-layout__hint-block {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.odc-add-page-layout__hint-block__text {
  flex: auto;
  margin-right: var(--pf-v5-global--spacer--lg);
  margin-bottom: var(--pf-v5-global--spacer--md);
}

.odc-add-page-layout__hint-block__actions {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.odc-add-page-layout__hint-block__actions>* {
  margin-right: var(--pf-v5-global--spacer--lg);
  margin-bottom: var(--pf-v5-global--spacer--md);
}

.odc-add-page-layout__hint-block__details-switch__loading-state {
  display: flex;
}

.odc-add-page-layout__hint-block__details-switch__text {
  font-size: 14px;
  font-weight: normal;
  align-items: center;
}

.odc-add-page-layout__additional-hint-block {
  padding-bottom: var(--pf-v5-global--spacer--md);
}

.odc-add-page-layout__resource-quota-message-block {
  margin-right: var(--pf-v5-global--spacer--xl);
}

