@media (prefers-reduced-motion: no-preference) {
  .odc-masonry-layout {
    transition: .25s ease-in-out;
  }
}

.odc-masonry-layout {
  display: flex;
  flex-direction: row;
  opacity: 0;
}

.odc-masonry-layout__allRendered {
  opacity: 1 !important;
}

.odc-masonry-layout__column {
  flex: 1;
  padding-left: var(--pf-v5-global--spacer--lg);
  margin-bottom: var(--pf-v5-global--spacer--xl);
  background-clip: content-box;
}

.odc-masonry-container {
  margin-left: calc(0px - var(--pf-v5-global--spacer--lg));
  width: auto;
}

