.odc-add-card-item {
  margin-top: var(--pf-v5-global--spacer--lg);
  margin-left: calc(var(--pf-v5-global--spacer--lg) - var(--pf-v5-global--spacer--sm));
}

.odc-add-card-item .pf-v5-c-simple-list__item-link {
  --pf-v5-c-simple-list__item-link--focus--FontWeight: normal;
  --pf-v5-c-simple-list__item-link--active--FontWeight: normal;
  --pf-v5-c-simple-list__item-link--hover--FontWeight: normal;
}

.odc-add-card-item__icon {
  height: var(--pf-v5-global--FontSize--lg);
  max-width: var(--pf-v5-global--spacer--lg);
  min-width: var(--pf-v5-global--spacer--md);
  color: var(--pf-v5-global--Color--100) !important;
  font-size: var(--pf-v5-global--FontSize--md);
  margin-right: var(--pf-v5-global--spacer--sm);
}

.odc-add-card-item__img-icon {
  vertical-align: -0.125em;
}

.odc-add-card-item__title {
  display: inline;
  vertical-align: middle;
  margin-left: var(--pf-v5-global--spacer--sm);
  font-size: var(--pf-v5-global--spacer--md) !important;
}

.odc-add-card-item__description {
  margin: var(--pf-v5-global--spacer--sm) 0 0 var(--pf-v5-global--spacer--sm);
  color: var(--pf-v5-global--Color--200);
  font-size: var(--pf-v5-global--FontSize--md);
}

.odc-add-card-item a {
  padding-left: 0;
  text-decoration: none;
}

:root:where(.pf-v5-theme-dark) .odc-add-card-item__img-icon {
  filter: brightness(1.5) invert(1) hue-rotate(180deg) saturate(4);
}

