.odc-add-card {
  margin-bottom: var(--pf-v5-global--spacer--lg);
  padding: 0px var(--pf-v5-global--spacer--lg) var(--pf-v5-global--spacer--lg) 0px;
  max-width: 100%;
}

.odc-add-card__title {
  margin: var(--pf-v5-global--spacer--lg) 0px 0px var(--pf-v5-global--spacer--lg);
}

.odc-add-card__icon {
  max-width: var(--pf-global--spacer--lg);
  min-width: var(--pf-global--spacer--md);
  color: var(--pf-global--Color--100) !important;
  font-size: var(--pf-global--FontSize--md);
  margin-right: var(--pf-global--spacer--sm);
}

.odc-add-card__img-icon {
  vertical-align: -0.3em;
}

.pf-v5-c-simple-list ul {
  list-style-type: none;
  padding-left: 0px;
  margin: 0px;
}

