.odc-add-section-skeleton-placeholder__container {
  max-width: 100%;
  padding: var(--pf-v5-global--spacer--lg) var(--pf-v5-global--spacer--lg) var(--pf-v5-global--spacer--2xl);
}

.odc-add-section-skeleton-placeholder__title {
  margin-top: var(--pf-v5-global--spacer--md) 0 var(--pf-v5-global--spacer--md) 0;
}

.odc-add-section-skeleton-placeholder__item-header {
  display: flex;
  margin-top: var(--pf-v5-global--spacer--xl);
}

.odc-add-section-skeleton-placeholder__item-header__title {
  margin-left: var(--pf-v5-global--spacer--sm);
}

.odc-add-section-skeleton-placeholder__item-desc {
  margin-top: var(--pf-v5-global--spacer--md);
}

.odc-add-section-skeleton-placeholder__item-desc>* {
  margin-top: var(--pf-v5-global--spacer--xs);
}

